body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.sidebar{position:fixed;top:0;left:0;height:100%;width:78px;background:#9f7884;padding:6px 14px;transition:all 0.9s ease}.sidebar .logo_content .logo{color:#fff;display:flex;height:5rem;width:100%;align-items:center;opacity:0;pointer-events:none;transition:all 0.9s ease}.sidebar .logo_content .logo .logo_name{font-size:1.62rem;font-weight:400;white-space:nowrap}.sidebar .logo_content>i{position:absolute;color:#fff;top:6px;left:50%;font-size:3rem;height:5rem;width:5rem;text-align:center;line-height:5rem;-webkit-transform:translateX(-50%);transform:translateX(-50%)}.sidebar ul{margin-top:4rem}.sidebar ul li{position:relative;height:5rem;width:100%;list-style:none;line-height:5rem;border-radius:1.2rem}.sidebar ul li a{color:#fff;display:flex;align-items:center;text-decoration:none;transition:all 0.1s ease;border-radius:1.2rem;white-space:nowrap}.sidebar ul li a:hover{color:#473b2a;background:#fff}.sidebar ul li a i{height:5rem;min-width:5rem;font-size:2rem;border-radius:1.2rem;line-height:5rem;text-align:center}.sidebar ul li a .links_name{opacity:0;pointer-events:none;font-size:1.5rem}.sidebar ul li .tooltip{position:absolute;left:122px;top:0;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);border-radius:6px;height:35px;width:122px;background:#fff;line-height:35px;font-weight:bold;font-size:1.3rem;text-align:center;box-shadow:0 5px 10px rgba(0,0,0,0.2);transition:0s;opacity:0;pointer-events:none}.sidebar ul li:hover .tooltip{transition:all 0.5s ease;opacity:1;top:50%}.sidebar.active{width:240px}.sidebar.active .logo_content .logo{opacity:1}.sidebar.active .logo_content>i{left:90%}.sidebar.active ul li .tooltip{display:none}.sidebar.active .links_name{opacity:1;pointer-events:auto}.sidebarContent.active{min-width:240px;transition:all 0.9s ease}.sidebarContent.active ~ .homeContent{min-width:calc(100vw - 240px)}

*{box-sizing:border-box;margin:0;padding:0}html{font-size:62.5%}body{background:#f0efeb;color:#473b2a;font-family:"Lato",sans-serif}

.mainContainer{display:flex;width:100vw}.sidebarContent{height:100vh;width:78px}.homeContent{width:calc(100vw - 78px);display:flex;flex-direction:column}.presentationContainer{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-around;align-items:center;height:100vh;width:100%}.presentationContainer img{width:27rem;height:27rem;border-radius:10rem;box-shadow:0px 0px 23px 0px rgba(0,0,0,0.6)}.presentationContainer .profile{display:flex;flex-direction:column;align-items:center;font-size:2.1rem;line-height:3.5rem}.presentationContainer .profile .redes{display:flex;width:15rem;justify-content:space-around}.presentationContainer .profile .redes a i{color:#473b2a;font-size:3.5rem;margin-top:50px}.dataContainer{display:flex;flex-direction:column;width:100%;justify-content:center;align-items:center;padding:4rem;font-size:1.87rem}.infoContainer{display:flex;flex-direction:column;align-items:center;max-width:85rem;min-width:65rem;padding:4rem;margin:4rem;background:#5a5766;box-shadow:0px 0px 23px 0px rgba(0,0,0,0.6);border-radius:20px;color:#fff;line-height:2.3rem}.infoContainer h3{text-align:center;margin-bottom:3rem;width:80%;border-bottom:#fff solid 2px}.infoContainer p{text-align:justify}

.contactContainer{display:flex;flex-direction:column;margin:2rem;padding:1rem}.contact{margin-bottom:3rem}.contact h1{font-size:2.4rem;line-height:3.5rem;padding:0 2px 2px 10px;border-bottom:solid 3px #9f7884}.contact p{font-size:medium}.listContainer ul{list-style:none}.listContainer ul li{display:flex;flex-direction:row;align-items:center}.listContainer ul li h2{margin-right:1.5rem;width:20rem;text-decoration:underline;padding-left:2rem}.listContainer ul li a{display:flex;flex-direction:row;align-items:center;font-size:1.8rem;text-decoration:none;color:#473b2a}.listContainer ul li a i{font-size:2.3rem;width:50px;height:50px;line-height:50px;text-align:center}

.navSkills{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-around;align-items:center;height:2rem;cursor:pointer;margin-top:1rem;transition:all ease 1s}.navSkills h3{padding:5px 1rem;font-size:small;border-bottom:solid transparent}.navSkills h3:hover{border-bottom:solid #b5838d 3.1px}.navSkills h3.activeTitle{border-bottom:solid #b5838d 3.1px;color:#9f7884}.iconsContainer{margin:5rem 5rem 0rem}

.textListContainer{color:#fff;display:flex;flex-direction:column;justify-content:center;align-items:center}.textListContainer .textListItem{width:80%;min-width:25rem;margin:1rem;padding:1rem;border-radius:1.5rem;min-height:5.5rem;display:flex;flex-direction:column;justify-content:center;align-items:flex-start}.textListContainer .textListItem h3{min-width:12rem;color:#473b2a}.textListContainer .textListItem .progress-line{position:relative;height:1.5rem;width:100%;border-radius:1rem;background-color:rgba(156,149,139,0.3);-webkit-transform:scaleX(0);transform:scaleX(0);-webkit-transform-origin:left;transform-origin:left;box-shadow:inset 0px 1px 1px rgba(0,0,0,0.5),0px 1px rgba(156,149,139,0.5);-webkit-animation:animate 1s cubic-bezier(1, 0, 0.5, 1) forwards;animation:animate 1s cubic-bezier(1, 0, 0.5, 1) forwards}.textListContainer .textListItem .progress-line span{position:absolute;height:100%;background:#9f7884;border-radius:10px;-webkit-transform:scaleX(0);transform:scaleX(0);-webkit-transform-origin:left;transform-origin:left;-webkit-animation:animate 1.3s cubic-bezier(1, 0, 0.5, 1) 1s forwards;animation:animate 1.3s cubic-bezier(1, 0, 0.5, 1) 1s forwards}@-webkit-keyframes animate{100%{-webkit-transform:scaleX(1);transform:scaleX(1)}}@keyframes animate{100%{-webkit-transform:scaleX(1);transform:scaleX(1)}}

.skillsIcons{width:100%;margin-top:auto;padding:1rem;display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-around;border-radius:1.5rem}.skillsIcons .icon_logo{display:flex;flex-direction:column;justify-content:space-around;align-items:center;border-radius:1rem;margin:1rem;padding:1rem;transition:all ease 0.5s}.skillsIcons .icon_logo:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}.skillsIcons .icon_logo img{background:#f0efeb;padding:1rem;border-radius:2rem;width:11rem;height:11rem;object-fit:contain}

.backgroundContainer{display:flex;flex-direction:column;margin:2rem;padding:1rem}.background{margin-bottom:3rem}.background h1{font-size:2.4rem;line-height:3.5rem;padding:0 2px 2px 10px;border-bottom:solid 3px #9f7884}.navBarBackground{margin-bottom:2rem}.navBarBackground ul{list-style:none;display:flex;flex-direction:row;justify-content:space-around;flex-wrap:wrap}.navBarBackground ul li{border:3px solid #9f7884;padding:5px;margin:1px 0;border-radius:1rem;text-align:center;width:20rem}.navBarBackground ul li .module-border-wrap{max-width:250px;padding:1rem;position:relative;background:linear-gradient(to right, #7400b8, #80ffdb);padding:5px;border-radius:1rem}.navBarBackground ul li .module-border-wrap .module{color:#222;background:#f0efeb;padding:2rem;border-radius:inherit}.navBarBackground ul li i{font-size:2.2rem}.navBarBackground ul li h3{font-size:1.4rem;-webkit-user-select:none;-ms-user-select:none;user-select:none}.navBarBackground ul li:hover{background-color:#9f7884;color:#fff;cursor:pointer}.activeButton{background-color:#9f7884;color:#fff;cursor:pointer}.dataTitle{border-bottom:solid 2px #9f7884}.dataTitle h3{font-size:2rem;padding:0 1rem 0.5rem 1rem}.generalData{margin-top:10px;width:70vw}.generalData ul{list-style:none}

.cardItem{display:flex;flex-direction:row;padding:2rem;margin:2rem 0;border-radius:1rem;box-shadow:0px 0px 23px 0px rgba(65,47,47,0.4)}.cardItem i{color:#9f7884;width:2rem;height:2rem;margin-right:1rem}.cardItem div{width:100%}.cardItem div .title{font-weight:bold;font-size:2rem;margin-bottom:2.5rem;cursor:default}.cardItem div .title .titleDetails{display:flex;flex-wrap:wrap;align-items:center;font-style:italic;font-size:1.4rem;margin-top:0.5rem}.cardItem div .title .titleDetails>div,.cardItem div .title .titleDetails a{width:auto;display:flex;align-items:center;font-style:italic;font-weight:400;font-size:1.4rem;margin-right:4rem;text-decoration:none;color:#473b2a;cursor:default}.cardItem div .title .titleDetails>div i,.cardItem div .title .titleDetails a i{font-size:2rem;line-height:2rem}.cardItem div .title .titleDetails>a{cursor:pointer}.cardItem div .title .titleDetails>a:hover p{text-decoration:underline}.cardItem div .details{display:block;width:100%;font-size:1.8rem;text-align:justify;padding-bottom:2rem;cursor:default}.cardItem div .skillsTagContainer{display:flex;flex-direction:row;flex-wrap:wrap}.cardItem div .skillsTagContainer .skillTag,.cardItem div .skillTag{display:inline;font-size:1.8rem;border:solid #9f7884 2px;color:#9f7884;border-radius:5px;margin:2px 2px;padding:2px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;cursor:default}.cardItem .logo{align-self:center;display:flex;justify-content:center;align-items:center;width:12rem;min-width:8rem;height:12rem;margin-left:3rem}.cardItem .logo img{width:100%}.cardItem .imgProject{height:30rem;box-shadow:0px 0px 17px 5px rgba(0,0,0,0.3);border-radius:1rem}.cardItem .imgProject img{width:100%;height:100%;object-fit:fill;border-radius:inherit}

